*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.catalog-container {
  padding-left: 20px;
  padding-right: 20px;
}

.catalog-section {
  flex-direction: column;
  display: flex;
}

.catalog-title {
  color: var(--dark-green);
  letter-spacing: -.8px;
  align-items: flex-start;
  margin-top: 0;
  margin-bottom: 64px;
  font-family: Epilogue;
  font-size: 40px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.1em;
}

.catalog-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 0;
  margin-bottom: 64px;
  display: flex;
}

.catalog-list .catalog-item {
  flex-basis: calc(9.09091% - 24px);
}

.catalog-caption {
  position: absulote;
}

.catalog-item {
  position: relative;
}

.catalog-item img {
  object-fit: cover;
  object-position: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 26px 0 25px;
  display: flex;
}

.content {
  color: var(--black);
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: -.28px;
  width: 276px;
  height: 18px;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28em;
  overflow: hidden;
}

.catalog-caption p {
  color: var(--lightblack);
  letter-spacing: -.24px;
  margin-top: 4px;
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.16em;
  position: absolute;
}

.content:hover {
  white-space: normal;
  height: auto;
  overflow: visible;
}

.catalog-figure {
  width: 276px;
  height: 325px;
  margin: 0;
  position: relative;
  overflow: hidden;
}

.catalog-figure figcaption h4 {
  color: var(--white, #fff);
  letter-spacing: -.28px;
  text-transform: uppercase;
  margin-bottom: 10px;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28em;
}

.catalog-figure figcaption {
  width: 100%;
  height: 100%;
  background-color: var(--dark-green);
  color: var(--figcaption);
  text-align: left;
  transition: var(--transition-duration) cubic-bezier(.4, 0, .2, 1);
  will-change: transform;
  border-radius: 8px;
  flex-direction: column;
  padding: 0 20px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.catalog-figure figcaption + p {
  margin-top: 4px;
}

.catalog-figure img {
  width: 276px;
  height: 325px;
  object-fit: cover;
  object-position: center;
  display: block;
}

.catalog-list .catalog-item:hover figcaption {
  cursor: pointer;
  transform: translateY(0);
}

.catalog-btn-container {
  flex-direction: row-reverse;
  align-items: flex-end;
  margin-top: 80px;
  display: flex;
}

.button-catalog {
  color: var(--white);
  text-align: center;
  text-transform: uppercase;
  background: var(--black);
  resize: none;
  cursor: pointer;
  border-radius: 8px;
  justify-content: flex-end;
  gap: 10px;
  padding: 18px 35px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: inline-flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

@media screen and (min-width: 375px) {
  .catalog-container {
    width: 375px;
    margin-left: 0;
    margin-right: auto;
  }
}

