  }

  .form-label-input:hover + .form-input-icon, .form-label-input:focus {
    fill: var(--iris);
    border: 1px solid var(--navy-blue, rgba(46, 47, 66, .4));
  }

  .modal-form-textarea {
    resize: none;
  }

  .modal-form-label {
    resize: none;
