    align-items: flex-start;
    display: flex;
  }

  .modal-form-label, .modal-form-textarea {
    display: flex;
    position: relative;
  }

  .form-label-textarea {
    width: 263px;
    height: 170px;
    resize: none;
    border: 1px solid rgba(20, 20, 20, .5);
    border-radius: 8px;
    align-items: flex-start;
    gap: 8px;
    margin-bottom: 20px;
    padding: 12px 16px;
    display: flex;
  }

  .visually-hidden {
    opacity: 0;
    border: 0;
    padding: 0;
    position: absolute;
  }

  .modal-form-button {
    color: var(--white);
    text-align: center;
    max-width: 263px;
    height: 50px;
    background-color: var(--dark-green);
    cursor: pointer;
    border-radius: 8px;
    justify-content: center;
    margin-bottom: 12px;
    padding: 18px 100px;
    display: block;
  }

  .modal-sign-up-form {
    flex-direction: column;
    margin-left: -250px;
    padding-top: 335px;
    padding-right: 0;
    display: inline-flex;
  }

  .modal-form-button:hover {
    background-color: var(--light-black);
    color: var(--white);
  }

  .button-description {
    width: 263px;
    height: 42px;
    color: rgba(20, 20, 20, .4);
    text-overflow: ellipsis;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
  }

  .button-description br {
    display: none;
  }
}

